/* ------------------------------------------------------------------------------------------------ */
/* Responsive */
/* ------------------------------------------------------------------------------------------------ */
.icon-mobilemenu {
  position: absolute;
  width: 28px;
  height: 28px;
  top: 18px;
  left: calc(100% + 40px);
  z-index: 4;
  display: none;
  transition: all .3s linear;
  span {
    display: block;
    width: 100%;
    height: 2px;
    position: absolute;
    opacity: 1;
    right: 0;
    background: $text;
    transition: all .3s linear;
    &:nth-child(1) {
      top: 8px; }
    &:nth-child(2) {
      top: 18px; }
    &:nth-child(3) {
      top: 28px; } } }

@include maxtablet() {
  .icon-mobilemenu {
    display: block; }
  [class^='col-'] {
    margin-bottom: 16px; }
  .sidebar {
    position: absolute;
    transition: all .3s ease-in-out;
    left: -250px; }
  .admin-content {
    padding-top: 80px; }
  .admin-content,
  .footer {
    transition: all .3s ease-in-out;
    margin-left: 0;
    width: 100%; }

  .footer {
    &__main {
      text-align: center;
      .header__logo {
        img {
          margin: 0 auto; } }
      .col-md-3 {
        &:nth-child(1) {
          order: 1; }
        &:nth-child(2) {
          order: 3; }
        &:nth-child(3) {
          order: 2;
          span {
            display: block;
            text-align: center; } } } } }

  .mobilemenu-show {
    .sidebar {
      left: 0; }
    .admin-content,
    .footer {
      margin-left: 250px; } }


  .page-heading {
    margin-bottom: 24px;
    flex-direction: column;
    align-items: flex-start;
    .left {
      width: 100%;
      order: 2; }
    .right {
      width: 100%;
      order: 1;
      margin-bottom: 24px;
      text-align: right; }
    h2 {
      margin-bottom: 0;
      font-size: 24px;
      line-height: 1.5em; }
    .btn {
      padding: 8px 12px;
      & + .btn {
        margin-left: 15px; } } }
  .section-heading {
    display: block;
    h3 {
      margin-bottom: 20px; }
    .right {
      display: block;
      .select,
      .search-box {
        width: 100% !important; }
      .select {
        margin-bottom: 16px; } } }

  .benefits-table {
    overflow-x: auto;
    .table {
      width: 800px; }
    .pagination-box {
      flex-direction: column;
      .pagination {
        order: 2;
        margin: 0 !important; }
      .select {
        order: 1;
        margin-bottom: 20px; } } }

  .wrap-box-card.listview {
    .box-card {
      flex-direction: column;
      &__img {
        margin: 0 auto;
        margin-bottom: 24px;
        img {
          margin: 0 auto; } }
      &__content {
        width: 100%;
        max-width: 100%; }
      &__footer {
        display: block;
        .author,
        .price {
          margin-bottom: 15px; } }
      .schedule {
        li {
          margin-bottom: 8px;
          display: block; }
        li + li {
          margin-left: 0; } } } } }
